* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
  perspective: 100px;
  background: #000;
}

#top {
  height: 15%;
  background-color: #000;
  border-bottom: 2px solid #01d5da;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

#top div {
  display: flex;
}

#main {
  height: 200%;
  perspective: 100px;
  transform-origin: top;
  background-color: #000;
  background-image: linear-gradient(0deg, #0000 0, #01d5da 2px, #0000 4px), linear-gradient(90deg, #0000 0, #01d5da 2px, #0000 4px);
  background-size: 64px 64px;
  justify-content: center;
  animation: 1.6s ease-in-out .3s forwards flyin, 1s ease-in-out 2.5s forwards return-to-normal;
  display: flex;
  position: relative;
}

#game #main {
  height: 200%;
  perspective: 100px;
  transform-origin: top;
  text-align: center;
  background-color: #000;
  background-image: linear-gradient(0deg, #0000 0, #01d5da 2px, #0000 4px), linear-gradient(90deg, #0000 0, #01d5da 2px, #0000 4px);
  background-size: 64px 64px;
  flex-direction: column;
  justify-content: center;
  animation: 1.6s ease-in-out .3s forwards flyin, 1s ease-in-out 2.5s forwards return-to-normal;
  display: flex;
  position: relative;
}

#main:before, #main:after {
  content: "";
  height: 10px;
  width: 200px;
  opacity: 0;
  background: #01d5da;
  animation: 5s ease-in 2s forwards lightwall;
  display: block;
  position: absolute;
  top: 32px;
  transform: rotateX(-10deg);
  box-shadow: 0 -5px 15px #01d5da;
}

#main:after {
  height: 10px;
  animation: 5s ease-in 2s lightwall;
  top: 500px;
  right: 0;
  box-shadow: 0 -10px 50px 2px #01d5da;
}

#menu {
  height: 100vh;
  width: 100vw;
}

#game {
  height: 100vh;
  width: 100vw;
  animation: 3s forwards fadeInAnimation;
}

#bottom {
  height: 10%;
  justify-content: center;
  display: flex;
  position: sticky;
  bottom: 0;
}

#quit {
  height: 30px;
  width: 100px;
}

img {
  height: 200px;
  aspect-ratio: 1 / 1;
  border-radius: 30%;
  margin: 50px 20px 20px;
}

.compImg {
  border: 2px solid #0000;
  border-radius: 30%;
  box-shadow: inset 0 0 60px 60px #01d5da, 0 0 60px orange;
}

.playerImg {
  border: 2px solid #0000;
  border-radius: 30%;
  box-shadow: inset 0 0 60px #01d5da, 0 0 60px #01d5da;
}

.hidden {
  display: none;
}

#player {
  color: #01d5da;
  margin-right: 5px;
}

#computer {
  color: orange;
}

h1 {
  text-transform: capitalize;
  color: #01d5da;
  order: 1;
}

h3 {
  text-transform: capitalize;
  color: #01d5da;
  order: 0;
}

#result {
  background-color: #000;
  font-size: 50px;
}

#resultContainer {
  height: 50px;
}

ol {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #01d5da;
  border: 2px solid #01d5da;
  order: 2;
  padding: 0 10px 0 20px;
}

li {
  text-transform: capitalize;
  color: #01d5da;
}

label {
  text-transform: capitalize;
  color: #01d5da;
  background-color: #000;
  font-size: x-large;
  font-weight: bolder;
}

form {
  height: 100px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 60px;
  display: flex;
}

#playerName {
  height: 25px;
}

#playerName:hover {
  box-shadow: 0 0 20px 10px #01d5da;
}

button {
  color: #01d5da;
  background-color: #000;
  padding: 5px 15px;
  font-size: medium;
}

button:hover {
  box-shadow: 0 0 15px 1px #01d5da;
}

@keyframes flyin {
  from {
    transform: translateY(-250px);
  }
}

@keyframes lightwall {
  0% {
    opacity: 0;
    width: 0;
  }

  5%, 50% {
    opacity: 1;
    width: 100%;
  }

  100% {
    opacity: 0;
    width: 100%;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes return-to-normal {
  to {
    height: 72%;
  }
}

